body {
  margin: 0;
  padding: 20px;
  background-color: #fff;
  color: #2a2a2a;
  line-height: 1.3;
  font-family: 'Helvetica Neue', -apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', sans-serif;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

ul {
  list-style: none;
}

p {
  margin: 0;
}

.App{
  text-align: center;
      width: 600px;
      margin-left: auto;
      margin-right: auto;
}